import { getPhoto } from './js/pixabay-api';
import { imgsTemplate } from './js/render-function';

const formEl = document.querySelector('.form-search');
const imagesUl = document.querySelector('.gallery');

formEl.addEventListener('submit', e => {
  e.preventDefault();
  const value = formEl.elements.request.value;
  getPhoto(value).then(data => {
    const markup = imgsTemplate(data);
    imagesUl.innerHTML(markup);
  });
});
